import { opportunityEnterprise } from '../../api/api.js'

export default {
	data() {
		return {
			id: '',
			province: '',
			city: '',
			district: '',
			content: '',
			createTime: '',
			title: ''
		}
	},
	onLoad(option){
		// 获取列表页传入的ID
		this.id = option.id
		// 获取企业招标详情数据
		opportunityEnterprise({
			id: this.id
		}).then(res => {
			console.log('res', res)
			let _d = res.data
			this.content = _d.content
			this.createTime = _d.createTime
			this.province = _d.province
			this.city = _d.city
			this.district =_d.district
			this.title = _d.title
		})			
	}
}
